/* General Styling for Body and HTML */
body,
html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

.App {
  text-align: center;
}

button {
  padding: 10px 20px;
  margin: 5px;
  cursor: pointer;
  background-color: #2167fd;
  color: white;
  border: none;
  border-radius: 5px;
}

button:hover {
  background-color: #1d1df2;
}

/* Navbar */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #333;
  color: white;
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.navbar-left img {
  width: 50px;
}

.navbar-links a {
  margin: 0 15px;
  color: white;
  text-decoration: none;
}

.navbar-links a:hover {
  text-decoration: underline;
}

/* Footer */
.footer {
  background: #333;
  color: white;
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
}

.footer-buttons button {
  background: none;
  color: white;
  border: none;
  margin: 0 15px;
  font-size: 16px;
  cursor: pointer;
}

.footer-buttons button:hover {
  text-decoration: underline;
}

/* --- Main Sections --- */
/* social media icons*/

.social-icon {
  text-decoration: none;
  margin: 0 10px;
  font-size: 24px;
}
.facebook {
  color: #3b5998;  /* Facebook official color */
}
.whatsapp {
  color: #1DA1F2;  /* Twitter official color */
}
.instagram {
  color: #C13584; /* Instagram official color */
}

/* Main Page */
.main-page {
  padding: 20px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.main-page > div {
  margin-bottom: 50px;
}

/* Centered Logo */
.centered-logo {
  margin-top: 20px;
}

.centered-logo img {
  width: 5%;
  display: block;
  margin: 0px auto;
}

/* Map & Phone Info */
.MainPageMapNnumber {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.map-info,
.phone-info {
  display: flex;
  align-items: center;
  margin: 10px 0;
}

.map-link,
.phone-link {
  text-decoration: none;
  color: black;
  margin-left: 5px;
}

/* Social Media Links */
.social-media-links {
  display: flex;
  justify-content: center;
  margin-top: 0px;
}

.social-media-links a {
  margin: 15px;
  color: #333;
  text-decoration: none;
}

.social-media-links a:hover {
  text-decoration: underline;
}

/* --- Specific Sections --- */

/* About Section */

.about-section {
  padding: 50px;
  margin-top: 80px;
  background-color: #eef;
  scroll-margin-top: 100px;
}

.About-US-section {
  background-color: #eef;
  scroll-margin-top: 100px;
}

/* Services Section */
.services-section {
  padding: 50px;
  margin-top: 80px;
  background-color: #eef;
  scroll-margin-top: 100px;
}

/* Feedback Section */
.Feedback-section {
  background-color: #eef;
  margin-top: 80px;
  scroll-margin-top: 100px;
}

.feedback-form {
  max-width: 500px;
  margin: 0 auto;
  text-align: center;
}
.Product-section {
  background-color: #eef;
  scroll-margin-top: 100px;
}
textarea {
  width: 100%;
  height: 100px;
  margin-top: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
}

/* Enquiry Section */
.Enquiry-section {
  padding: 50px;
  margin-top: 80px;
  background-color: #eef;
  scroll-margin-top: 100px;
}

.form-container {
  max-width: 500px;
  margin: 0 auto;
  text-align: center;
}

form input,
form textarea {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
}

/* Gallery Section */
.Gallery-section {
  padding: 50px;
  margin-top: 80px;
  background-color: #eef;
  scroll-margin-top: 100px;
}

/* Payment Section */
.Payment-section {
  padding: 20px;
  margin-top: 80px;
  background-color: #eef;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  scroll-margin-top: 100px;
}

.Payment-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
}

.Payment-details img {
  max-width: 150px;
  width: 100%;
  height: auto;
  margin-bottom: 10px;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.Payment-details p {
  font-size: 1.1rem;
  font-weight: bold;
  color: #333;
  margin-top: 10px;
  text-align: center;
}

/* Share Section */
.Share-section {
  padding: 20px;
  margin-top: 80px;
  background-color: #eef;
  text-align: center;
  border-radius: 10px;
  margin-bottom: 40px;
  scroll-margin-top: 100px;
}

.buttonImageLink {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
}

.buttonImageLink button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
}

.buttonImageLink button:hover {
  background-color: #0056b3;
}

.buttonImageLink img {
  max-width: 200px;
  width: 100%;
  height: auto;
  margin-bottom: 10px;
  border-radius: 10px;
}
/* footer section */




/* Container holding all images */
.image-container {
  display: flex;
  flex-direction: column; /* Vertically aligns images */
  align-items: center; /* Centers the images horizontally */
  width: 100%; /* Full width on mobile */
  padding: 10px; /* Optional padding */
}

.image-container img {
  max-width: 100%; /* Ensures images are responsive */
  height: auto; /* Maintains image aspect ratio */
  margin-bottom: 20px; /* Adds space between images */
}
/* --- Mobile Styling --- */
@media only screen and (max-width: 768px) {
  /* Main Page */
  .main-page {
    padding: 15px;
  }

  /* Payment Section */
  .Payment-details img {
    max-width: 120px;
  }

  .Payment-details p {
    font-size: 1rem;
  }

  /* Share Section */
  .buttonImageLink img {
    max-width: 150px;
  }

  /* Responsive adjustments for buttons */
  .buttonGroup {
    flex-direction: column;
    gap: 15px;
  }
}
